









import Vue from 'vue'
export default Vue.extend({
	data() {
		return {
			keys: [] as any[],
			values: [] as any[],
		}
	},
	props: {
		item: {
			type: Object,
			default: () => ({}),
		},
	},
	watch: {
		item() {
			const d = { ...this.item }
			delete d['id']
			this.keys = Object.keys(d)
			this.values = Object.values(d)
		},
	},
	mounted() {
		if (Object.keys(this.item).length) {
			const d = { ...this.item }
			delete d['id']
			this.keys = Object.keys(d)
			this.values = Object.values(d)
		}
	},
})
