






















































import Vue from 'vue'
import Card from '~/components/Card.vue'

const items = [
	{
		id: '1',
		name: 'laptop1',
	},
	{
		id: '2',
		name: 'laptop2',
	},
	{
		id: '3',
		name: 'laptop3',
	},
	{
		id: '4',
		name: 'laptop4',
	},
	{
		id: '5',
		name: 'laptop5',
	},
	{
		id: '6',
		name: 'laptop6',
	},
	{
		id: '7',
		name: 'laptop7',
	},
	{
		id: '8',
		name: 'laptop8',
	},
]

export default Vue.extend({
	data() {
		return {
			search: '',
			foundItems: [] as any[],
			item1: {} as any,
			item2: {} as any,
			showList: true,
			count: 0,
		}
	},
	components: {
		Card,
	},
	methods: {
		recommendations() {
			this.$axios
				.get(this.$apiUrl.recommendUrl())
				.then((response) => {
					this.foundItems = response.data
					this.showList = true
				})
				.catch((e) => {
					this.$toastErrors(this, e)
				})
		},
		clickOutside(e: any) {
			if (!this.showList) return
			if (
				// @ts-ignore
				!this.$refs.search?.contains(e.target)
			) {
				this.showList = false
			}
		},
		searching() {},
		submit() {
			this.$axios
				.get(this.$apiUrl.searchUrl(this.search))
				.then((response) => {
					this.foundItems = response.data.results
					this.showList = true
				})
				.catch((e) => {
					this.$toastErrors(this, e)
				})
		},
		itemClicked(item: any) {
			this.$axios
				.put(this.$apiUrl.registerUrl(item.id), {
					...item,
					numberofsearch: Number(item.numberofsearch) + 1,
				})
				.then((response) => {
					this.showList = false
					const d = { ...item }
					delete d['numberofsearch']
					if (this.count === 0) {
						this.item1 = d
						this.count++
					} else {
						this.item2 = d
						this.count = 0
					}
				})
				.catch((e) => {
					this.$toastErrors(this, e)
				})
		},
		clear() {
			this.search = ''
			this.recommendations()
		},
	},
	mounted() {
		if (process.browser) {
			document.addEventListener('click', this.clickOutside)
		}
		this.$axios
			.get(this.$apiUrl.recommendUrl())
			.then((response) => {
				this.foundItems = response.data
			})
			.catch((e) => {
				this.$toastErrors(this, e)
			})
	},
	destroyed() {
		if (process.browser) {
			document.removeEventListener('click', this.clickOutside)
		}
	},
})
